const set_cookie = function  ( name, value, exp_y, exp_m, exp_d, path, domain, secure )
{
    var cookie_string = name + "=" + escape ( value );

    if ( exp_y )
    {
        var expires = new Date ( exp_y, exp_m, exp_d );
        cookie_string += "; expires=" + expires.toGMTString();
    }

    if ( path )
        cookie_string += "; path=" + escape ( path );

    if ( domain )
        cookie_string += "; domain=" + escape ( domain );

    if ( secure )
        cookie_string += "; secure";

    document.cookie = cookie_string;
};
const  delete_cookie = function  ( cookie_name )
{
    var cookie_date = new Date ( );  // Текущая дата и время
    cookie_date.setTime ( cookie_date.getTime() - 1 );
    document.cookie = cookie_name += "=; expires=" + cookie_date.toGMTString();
};
const get_cookie = function( cookie_name )
{
    var results = document.cookie.match ( '(^|;) ?' + cookie_name + '=([^;]*)(;|$)' );

    if ( results )
        return ( unescape ( results[2] ) );
    else
        return null;
};

let current_date = new Date;
let cookie_year = current_date.getFullYear ( ) ;
let cookie_month = current_date.getMonth ( );
let cookie_day = current_date.getDate ( ) + 2;// save for 2 day - change how you need//

import userData from '../test_user_data';
export  default {
    actions:{
        doAuthAction(ctx , payload){
            if(payload.logo  === userData.logo  && payload.password === userData.password){
              // set_cookie('logo' , userData.logo, cookie_year, cookie_month, cookie_day);
              // set_cookie('password' , userData.password, cookie_year, cookie_month, cookie_day);
               ctx.commit('setUserData' , userData );
               ctx.commit('setIsAuth' , true);

            }else{
                ctx.commit('setIsAuth' , false);

            }
        },
        doLogoutAction(ctx){
            // delete_cookie('logo');
            // delete_cookie('password');
            ctx.commit('setUserData' , {} );
            ctx.commit('setIsAuth' , false);
        },
        doCheckAuthAction(ctx){
            const logo = get_cookie('logo');
            const password = get_cookie('password');
            if( logo && password &&
                logo === userData.logo &&
                password === userData.password ){
                // if user enter site - add Auth cookie  auto
                // set_cookie('logo' , userData.logo , userData.password, cookie_year, cookie_month, cookie_day);
                // set_cookie('password' , userData.password , userData.password, cookie_year, cookie_month, cookie_day);
                ctx.commit('setIsAuth' , true);
                ctx.commit('setUserData' , userData );
            }else{
                ctx.commit('setIsAuth' , false );
            }
        },
    },
    mutations:{
        setIsAuth(state , isAuth){
            state.isAuth = isAuth;
        },
        setUserData(state , userData){
            if( userData.length === 0 ){
                delete_cookie('logo');
                delete_cookie('password');
            }else{
                // if user enter site - add Auth cookie  auto
                set_cookie('logo' , userData.logo , userData.password, cookie_year, cookie_month, cookie_day);
                set_cookie('password' , userData.password , userData.password, cookie_year, cookie_month, cookie_day);
            }
            state.user =userData;
        }
    },
    state:{
       isAuth: false,
        user: {}
    },
    getters:{
        isAuthGetter(state){
            return state.isAuth;
        },
        userDataGetter(state){
            return state.user;
        }
    },
}
