import Vue from 'vue';
import Router from "vue-router";

import FrontPage from "../pages/FrontPage";
import Home from "../pages/Home";
import  Gallery from "../pages/Gallery";

Vue.use(Router);

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            component: FrontPage,
        },
        {
            path: '/posts',
            component: Home
        },
        {
            path: '/gallery',
            component: Gallery
        }
    ]
})
