<template>
    <div v-if="isAuthGetter" class="Gallery">
        <h1>Gallery</h1>
<!--        <button @click="handlerGetGalleryFromApi" class="btn btn-success">get gallery from Api</button>-->
        <hr/>
        <label >
            <span>Selected {{ selectCount }} photos</span><br/>
            <input v-model="selectCount" @change="handlerGetGalleryFromApi" type="range" min="1" max="100" step="1"/>
            <input v-model="selectCount" @change="handlerGetGalleryFromApi" type="number"/>
        </label>
        <Loader v-if="isLoaderGetter"/>
        <ImageGallery v-if="!isLoaderGetter"/>
    </div>
</template>

<script>
import  ImageGallery  from "../../components/common/ImageGallery"
import  Loader from "../../components/common/Loader";
import { mapGetters , mapActions} from  'vuex';
    export default {
        name: "Gallery",
        components:{
            Loader, ImageGallery
        },
        computed:{
            ...mapGetters(['isAuthGetter', 'isLoaderGetter'])
        },
        methods:{
            ...mapActions(['fetchGalleryApiAction']),
            handlerGetGalleryFromApi(){
              this.fetchGalleryApiAction(this.selectCount);
              // other case  invoke  action
            //  this.$store.dispatch("fetchGallery" , this.selectCount);
            },
        },
        mounted() {
            this.handlerGetGalleryFromApi();
},
        data(){
            return{
                selectCount: 15
            }
        },
        props: {
        },
    };
</script>
