export  default {
    actions:{
        async  fetchGalleryApiAction(ctx , limit = 10){
            ctx.commit('updateIsLoader' , true);
            const res = await fetch(
                `https://jsonplaceholder.typicode.com/photos?_limit=${limit}`
            );
            const gallery = await res.json();
            ctx.commit("updateIsLoader" , false);
            ctx.commit('updateGallery' , gallery);
        }
    },
    mutations:{
        updateGallery(state , gallery){
            state.gallery = gallery;
        },
        updateIsLoader(state , isLoader){
           state.isLoader  = isLoader;
        }
    },
    state:{
        isLoader: false,
        gallery:[],
    },
    getters:{
        galleryGetter(state){
            return state.gallery;
        },
        isLoaderGetter(state){
            return state.isLoader;
        }
    },
}
