<template>
    <div @click="handlerClickLogout" class="AuthLogout">
        <i class="fas fa-sign-out-alt"></i>
    </div>
</template>

<script>
import  { mapActions } from 'vuex';
    export default {
        props: {
        },
        methods:{
            ...mapActions(['doLogoutAction']),
            handlerClickLogout(){
               this.doLogoutAction();
            }
        }
    };
</script>
