<template>
    <form @submit.prevent="handlerOnSubmitLogIn" class="AuthForm py-1 rounded">
        <input @input="setUserLogo($event.target.value)" v-model.trim="userLogo" class="form-control my-1" placeholder="User name" type="text"/>
        <div class="error" v-if="!$v.userLogo.required">Field is required</div>
        <div class="error" v-if="!$v.userLogo.minLength">
            Минимальная длина  {{$v.userLogo.$params.minLength.min}} знаков.
        </div>
        <div class="error" v-if="!$v.userLogo.maxLength">
            Максимальная длина  {{$v.userLogo.$params.maxLength.max}} знаков.
        </div>

        <input @input="setUserPass($event.target.value)" v-model.trim="userPass" class="form-control my-1" placeholder="Password" type="password"/>
        <div class="error" v-if="!$v.userPass.required">Field is required</div>
        <div class="error" v-if="!$v.userPass.minLength">
          Минимальная длина  {{$v.userPass.$params.minLength.min}} знаков.
        </div>
        <div class="error" v-if="!$v.userPass.maxLength">
           Максимальная длина  {{$v.userPass.$params.maxLength.max}} знаков.
        </div>
        <div v-if="!isSuccessAuth" class="error">Неверный логин или пароль.</div>

        <button type="submit" class="btn btn-success mt-1 ml-auto">Log in</button>
    </form>
</template>

<script>
import { required, minLength , maxLength } from 'vuelidate/lib/validators'


import  { mapActions , mapGetters } from 'vuex';
    export default {
        name: 'AuthForm',
        props: {
        },
        computed:{
...mapGetters(['isAuthGetter']),
        },
        data(){
          return {
              userLogo: '',
              userPass: '',
              isSuccessAuth: true,
          }
        },
        methods:{
            ...mapActions(['doAuthAction']),
            handlerOnSubmitLogIn(){
                if(
                    this.userLogo &&
                    this.userPass
                    && this.$v.userLogo.minLength
                    && this.$v.userLogo.maxLength
                    && this.$v.userPass.minLength
                    && this.$v.userPass.maxLength
                ){
                    this.doAuthAction({ logo:this.userLogo , password:this.userPass}) ;
                    this.isSuccessAuth = this.isAuthGetter;
                }
            },
            setUserLogo(value) {
                this.userLogo = value;
                this.$v.userLogo.$touch();
            },
            setUserPass(value) {
                this.userPass = value;
                this.$v.userPass.$touch();
            }
        },
        validations: {
            userLogo: {
                required,
                minLength: minLength(3),
                maxLength: maxLength(20) ,
            },
            userPass: {
                required,
               // between: between(3, 25)
                minLength: minLength(3),
                maxLength: maxLength(20)
            }
        },
    };
</script>
<style scoped>
    .AuthForm{
        width: 250px
    }
    .error{
        color: red;
    }
</style>
